.contact {
  background: #111111;
  color: #ffffff;
  margin-bottom: -50px;
  margin-top: 0;
  padding: 0;
}
.form {
  border-top: 2px solid #555;
  border-left: 2px solid #555;
  border-bottom: 2px solid #555;
  padding: 20px;
  display: flex;
  margin-bottom: 50px;
}
.projectInfo {
  margin: 0 60px;
}
.allcontacts {
  background: #555;
  border-top: 2px solid #f6ff00;
  border-right: 2px solid #f6ff00;
  border-bottom: 2px solid #f6ff00;
  color: #fff;
  padding: 20px;
  margin-bottom: 50px;
}
.input {
  transition: 0.6s all ease;
  padding: 10px 10px;
  font-size: large;
  color: #fff;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid #fff;
  outline: none;
  background: transparent;
}
.input:focus {
  border-bottom: 2px solid rgb(0, 128, 255);
}
.textarea {
  transition: 0.6s all ease;
  padding: 10px 10px;
  width: 20%;
  font-size: large;
  resize: none;
  outline: none;
  background: transparent;
  color: #fff;
}
.submitBtn {
  background: rgb(30, 30, 30);
  border: none;
  padding: 10px;
  font-size: large;
  color: #fff;
  border-radius: 5px;
}
.contactIcon {
  display: flex;
  align-items: center;
  margin: 10px 0;
}
.contactIcon > i {
  margin-right: 10px;
  padding: 5px 10px;
  background: #000;
  border-radius: 10px;
  font-size: 29px;
}
.contactIcon > span {
  font-size: 20px;
}
@media (max-width: 1200px) {
  .form {
    display: block;
    text-align: center;
    margin-bottom: 0;
  }
  .projectInfo {
    margin: 20px 0 0 0;
  }
}
@media (max-width: 600px) {
  .input {
    width: 80%;
  }
}
