* {
  margin: 0;
  padding: 0;
}
.heading {
  background: url("https://cutewallpaper.org/27/black-furtsitic-wallpaper/data-technology-illustration-abstract-futuristic-background-wave-with-connecting-dots-and-lines-on-dark-background-wave-of-particles-prytek.jpeg");
  background-size: cover;
  background-position: center;
  color: #fff;
  padding: 20px;
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.headingtxt {
  text-align: center;
}
.headingtxt > h1 {
  font-size: 35px;
  text-transform: uppercase;
  margin-bottom: 5px;
  background: rgb(251, 235, 63);
  background: radial-gradient(
    circle,
    rgb(255, 255, 255) 0%,
    rgb(255, 242, 0) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.headingtxt > span {
  text-transform: uppercase;
  padding-bottom: 10px;
  font-size: large;
  border-bottom: 2px solid red;
}
.btns-group {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.btn-succsess {
  position: relative;
  background: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  font-weight: 400;
  padding: 10px 30px;
  transition: 0.5s;
  margin: 0 10px;
  background-color: #2dff1e;
  box-shadow: 0 0 35px #2dff1e;
  color: #2dff1e;
}
.btn-succsess:hover {
  background-color: #ff0000;
  letter-spacing: 0.2rem;
  box-shadow: 0 0 35px #ff0000;
  color: #ff0000;
}
.btn-succsess::before {
  content: "";
  position: absolute;
  inset: 2px;
  background: #27283c;
}
.btn-succsess > span {
  position: relative;
  z-index: 1;
}
.btn-succsess > i {
  position: absolute;
  inset: 0;
  display: block;
}
.btn-succsess > i::before {
  content: "";
  position: absolute;
  top: 0;
  left: 80%;
  width: 20px;
  height: 4px;
  background: #27283c;
  transform: translateX(-50%) skewX(325deg);
  transition: 0.5s;
}
.btn-succsess:hover i::before {
  width: 20px;
  left: 20%;
}
