.mybtn {
  text-align: center;
  position: relative;
  display: block;
  color: rgb(255, 255, 255);
  font-size: 16px;
  font-weight: 600;
  min-width: 50%;
  font-family: "montserrat";
  text-decoration: none;
  margin: 10px 0;
  border: 2px solid rgb(0, 0, 0);
  padding: 7px 15px;
  text-transform: uppercase;
  overflow: hidden;
  transition: 1s all ease;
}
.mybtn::before {
  background: rgb(0, 0, 0);
  color: #fff;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.6s ease;
}

.mybtn::before {
  width: 100%;
  color: #fff;
  height: 0%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.mybtn:hover::before {
  height: 380%;
  color: #fff;
}
