.infoAboutCompany {
  font-weight: 300;
  color: #0e0e0e;
  font-size: x-large;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.capitalize {
  text-transform: lowercase;
}
b {
  font-weight: 600;
  font-size: x-large;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.about-banner {
  background: url("https://images.squarespace-cdn.com/content/v1/5b43df22b40b9d355958dc25/1564679669981-HE05040FSX9PG2VDIYAX/10_pluralsight_mob_programming_2.jpg");
  background-size: cover;
  width: 80%;
  margin: 1rem auto;
  border-radius: 10px;
}
.about-details {
  background: rgba(0, 0, 0, 0.748);
  width: 100%;
  margin: 1rem auto;
  padding: 20px 40px;
  height: 60vh;
  padding: 20px 40px;
  color: #fff;
  border-radius: 10px;
  display: flex;
  align-items: end;
  justify-content: center;
}
.details {
  margin: 0 10px;
  width: 30%;
  font-size: x-large;
  display: flex;
  justify-content: center;
}
.details > div > h4 {
  font-weight: 300;
}
.allInfo {
  width: 80%;
  margin: 1rem auto;
  background: #5f5f5f;
  color: #fff;
}
.colmsDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #606060;
}
.title {
  color: #2bff00;
  margin: 10px 0;
  text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3), 0px -4px 10px #646464;
  font-size: xx-large;
}
.colmsDiv:nth-child(1) {
  padding: 10px;
}
.contactbtn {
  margin: 10px 0;
  text-align: center;
  min-width: 15%;
  border: 2px solid #fff;
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  font-size: x-large;
  transition: 1s all ease;
}
.contactbtn:hover {
  transition: 1s all ease;
  border: 2px solid rgb(251, 255, 0);
  color: rgb(251, 255, 0);
  letter-spacing: 0.15rem;
}
.footer-icons {
  margin-top: 5px;
}

.footer-icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;
  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;

  margin-right: 3px;
  margin-bottom: 5px;
}

@media (max-width: 765px) {
  .about-banner,
  .allInfo {
    width: 95%;
    background-position: center;
    margin: 0.5rem auto;
  }
  .about-details {
    display: block;
    margin: 0 auto;
  }
  .details {
    font-size: xx-large;
    width: 100%;
    margin: 35px 0 0 0;
    display: flex;
    align-items: center;
  }
  .details > div > h4 {
    font-size: x-large;
  }
  .details:nth-child(3) {
    margin-left: -10px;
  }
}
