.home {
  background: url("https://cutewallpaper.org/27/black-furtsitic-wallpaper/data-technology-illustration-abstract-futuristic-background-wave-with-connecting-dots-and-lines-on-dark-background-wave-of-particles-prytek.jpeg");
  background-position: center;
  height: 70vh;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
}
.anim-texts {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 50px;
  text-align: start;
}

.anim-texts > h1 > span:first-child {
  background: rgb(251, 235, 63);
  font-size: 50px;
  background: radial-gradient(
    circle,
    rgb(255, 255, 255) 0%,
    rgb(255, 242, 0) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 50px;
  -webkit-text-fill-color: transparent;
}
.anim-texts > h1 > .line {
  margin: 0 10px;
  font-size: 50px;
}
.animated-text {
  height: 70px;
  overflow: hidden;
  position: relative;
}

.animated-text h3 {
  background: rgb(251, 63, 63);
  font-size: 50px;
  background: radial-gradient(
    circle,
    rgb(51, 0, 255) 0%,
    rgb(179, 0, 255) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0 10px;
  font-weight: 700;
  line-height: 70px;
  letter-spacing: 1px;
}

.animated-text h3:nth-child(1) {
  animation: text-move 10s infinite;
}

@keyframes text-move {
  0% {
    margin-top: 0;
  }

  25% {
    margin-top: -70px;
  }

  50% {
    margin-top: -140px;
  }

  75% {
    margin-top: -70px;
  }

  100% {
    margin-top: 0;
  }
}
.btns-group {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
@media (max-width: 1082px) {
  .anim-texts {
    font-size: 30px;
  }
  .animated-text h3 {
    font-size: 30px;
  }
  .anim-texts > h1 > span:first-child {
    font-size: 30px;
  }
  .anim-texts > h1 > .line {
    font-size: 30px;
  }
}
@media (max-width: 666px) {
  .home {
    background-position: bottom;
    height: 60vh;
    display: flex;
    align-items: center;
  }
  .anim-texts {
    display: block;
    text-align: center;
  }
  .anim-texts {
    font-size: 35px;
  }
  .animated-text h3 {
    font-size: 35px;
  }
  .anim-texts > h1 > span:first-child {
    font-size: 35px;
  }
  .anim-texts > h1 > .line {
    display: none;
  }
  .btn-primary,
  .btn-succsess {
    font-size: 20px;
  }
}
